.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-decrease {
  display: inline-block;
  padding: 6px;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
}
.cartcontrol .cart-decrease.move-transition {
  opacity: 1;
  -webkit-transform: translate3D(0, 0, 0);
          transform: translate3D(0, 0, 0);
}
.cartcontrol .cart-decrease.move-transition .inner {
  display: inline-block;
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.cartcontrol .cart-decrease.move-enter,
.cartcontrol .cart-decrease.move-leave {
  opacity: 0;
  -webkit-transform: translate3D(24px, 0, 0);
          transform: translate3D(24px, 0, 0);
}
.cartcontrol .cart-decrease.move-enter .inner,
.cartcontrol .cart-decrease.move-leave .inner {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}
.cartcontrol .cart-count {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  padding-top: 6px;
  line-height: 24px;
  text-align: center;
  font-size: 10px;
  color: #93999f;
}
.cartcontrol .cart-add {
  display: inline-block;
  padding: 6px;
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
}


