  vertical-align: top;
  width: 30px;
  height: 18px;
  background-size: 30px 18px;
  background-image: url("brand@2x.png");
  background-repeat: no-repeat;
}
@media (-weblit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
